<template>
  <div class="test-collection">
    <div class="head">
      <div class="back" @click="$router.go(-1)">返回</div>
      <p>采集清单</p>
      
    </div>
    <div class="test">
      <div class="link">
        <input type="text" placeholder="测试采集链接" v-model="link">
        <div class="btn" @click="test">测试采集</div>
      </div>
      <div class="result">测试采集结果</div>
      <table>
        <tr v-for="(item,index) in list" :key="index">
          <td class="name">{{item.title}}</td>
          <td>作者：{{item.author}}}</td>
          <td>采集了120章</td>
          <td>查看</td>
        </tr>
        <tr>
          <td class="name">七届传说</td>
          <td>作者：李小明</td>
          <td>采集了120章</td>
          <td>查看</td>
        </tr>
        <tr>
          <td class="name">七届传说</td>
          <td>作者：李小明</td>
          <td>采集了120章</td>
          <td>查看</td>
        </tr>
      </table>
    </div>
    <div class="nomsg" v-if="list==[]">没有采集到数据</div>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  data(){
    return{
      link:"",//链接
      taskmsg:"",
      list:[]
    }
  },
  methods:{
    test(){
      console.log(this.taskmsg)
      let token=sessionStorage.getItem("token");
      let url="http://zhuishu.zhiask.cn/api";
      let data={
        "action": "test_crawler",
        "data": {
            "id": this.taskmsg.id,
            "link": this.link
        }
      }
      if(this.link==""){
        this.$message.error("测试链接不能为空");
        return;
      }
      axios({
        method: 'post',
        url: url,
        data: JSON.stringify(data),
        headers:{
          "Content-Type": "application/json" ,//multipart/form-data;boundary=--xxxxxxx   application/json
          "Access-Token":token
        },
        contentType: "text/html;charset=utf-8",
        dataType: "json",
      })
      .then(res=>{
        console.log(res.data);
        this.list=res.data.data;
        if(res.data.data==null){
          this.$message.error("没有采集到相关数据")
        }
      })
    }
  },
  created(){
    this.taskmsg= JSON.parse(sessionStorage.getItem("taskmsg")) 
  }
}
</script>
<style>
  a,div,img,span,p,input,h1,h2,h3,h4,h5,h6,header,body,footer,ul,li{
  margin:0px;
  padding:0px;
}
.test-collection{
  padding:30px 0px 0px 0px;
  width: 97%;
  margin-left: 32px;
}
.test-collection .head{
  width: 100%;
  position: relative;
  padding-bottom: 15px;
  border-bottom: 1px solid rgba(187, 187, 187, 1);
}
.test-collection .head p{
  width: 80px;
  height: 29px;
  color: rgba(16, 16, 16, 1);
  font-size: 16px;
  font-family: SourceHanSansSC-regular;
}
.test-collection .head .back{
  width: 80px;
  height: 23px;
  border-radius: 4px;
  color: rgba(16, 16, 16, 1);
  font-size: 14px;
  line-height: 23px;
  text-align: center;
  font-family: Microsoft Yahei;
  border: 1px solid rgba(187, 187, 187, 1);
  cursor: pointer;
  margin-bottom: 10px;
}
.test-collection .add{
  margin-top:20px;
}
.test-collection .add input{
  width: 414px;
  height: 31px;
  color: rgba(136, 136, 136, 1);
  font-size: 14px;
  text-align: left;
  font-family: Microsoft Yahei;
  border: 1px solid rgba(187, 187, 187, 1);
  border-radius: 8px;
  outline: none;
  padding-left: 10px;
  margin-right: 10px;
}
.test-collection .add .search-btn{
  display: inline-block;
  width: 80px;
  height: 31px;
  line-height: 31px;
  border-radius: 8px;
  color: rgba(16, 16, 16, 1);
  font-size: 14px;
  text-align: center;
  font-family: Microsoft Yahei;
  border: 1px solid rgba(187, 187, 187, 1);
  cursor: pointer;
}
.test-collection .test{
  width: 100%;
  padding:0px 0px 0px 0px;
  margin-top:20px;
}
.test-collection .test .link{
  display:flex;
}
.test-collection .test .link input{
  width: 290px;
  height: 34px;
  color: rgba(136, 136, 136, 1);
  font-size: 14px;
  text-align: left;
  font-family: Microsoft Yahei;
  border: 1px solid rgba(187, 187, 187, 1);
  margin-right: 21px;
  padding-left:5px;
  border-radius: 5px;
  outline: none;
}
.test-collection .test .link .btn{
  width: 97px;
  height: 34px;
  line-height: 34px;
  border-radius: 4px;
  color: rgba(16, 16, 16, 1);
  font-size: 14px;
  text-align: center;
  font-family: Microsoft Yahei;
  border: 1px solid rgba(187, 187, 187, 1);
  cursor: pointer;
}
.test-collection .test .result{
  width: 100%;
  height: 20px;
  color: rgba(16, 16, 16, 1);
  font-size: 14px;
  text-align: left;
  padding:15px 0px 5px 0px;
  font-family: SourceHanSansSC-regular;
  border-bottom: 1px solid rgba(187, 187, 187, 1);
  margin-bottom: 10px;
}
.test-collection .test table{
  width: 608px;
  border-bottom: 1px solid rgba(187, 187, 187, 1);
  padding-bottom: 8px;
  border-collapse: collapse;
  margin-left:2px;
}
.test-collection .test table td{
  width: 30%;
  height: 20px;
  color: rgba(16, 16, 16, 1);
  font-size: 14px;
  line-height: 20px;
  font-family: SourceHanSansSC-regular;
  border-bottom: 1px solid rgba(187, 187, 187, 1);
  padding: 10px 0px;;
  margin-top:5px;
  text-align: center;
  cursor: pointer;
}
.test-collection .test table .name{
  width:20%;
  text-align: left;
}
.test-collection .nomsg{
  width: 300px;
  height: 30px;
  text-align: center;
  margin:10px auto;
  display: none;
}
</style>


