<template>
  <div class="atm">
    <div class="top">
      <div class="back" @click="$router.go(-1)">返回</div>
      <div class="creat">新建采集任务</div>
      <div class="clear" @click="xinjian">清空采集任务</div>
    </div>
    <table>
      <thead>
        <td class="head name" >任务名称</td>
        <td class="head cz">操作</td>
      </thead>
      <tr v-for="(item,index) in list" :key="index">
        <td class="name" >{{item.title}}</td>
        <td class="cz">
          <div class="see" @click="$router.push({path:'bookDetails'})">查看</div>
          <div class="del" @click="deleta(index)">删除</div>
        </td>
      </tr>
    </table>
    <div class="fenye">
       <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pageNum+1"
        :page-sizes="[10, 20, 30, 40, 50, 100]"
        :page-size="10"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  data(){
    return{
      taskmsg:"",//采集方案信息
      pageNum:1,//页码
      pageSize:10,
      total:0,//总数
      list:[]
    }
  },
  methods:{
    gettasklist(){
      let token=sessionStorage.getItem("token");
      let url="http://zhuishu.zhiask.cn/api";
      let data={
        "action": "list_task",
        "data": {
          "id": this.taskmsg.id,
          "page": this.pagenum,
          "num": this.pagesize
        }
      }
      axios({
        method: 'post',
        url: url,
        data: JSON.stringify(data),
        headers:{
          "Content-Type": "application/json" ,//multipart/form-data;boundary=--xxxxxxx   application/json
          "Access-Token":token
        },
        contentType: "text/html;charset=utf-8",
        dataType: "json",
      })
      .then(res=>{
        console.log(res.data.data);
        this.list=res.data.data;
      })
    },
    //分页
    handleSizeChange(e){
      this.pageSize=e;
      this.gettasklist();
    },
    handleCurrentChange(e){
      this.pageNum=e-1;
      this.gettasklist();
    },
    //删除
    deleta(index){
      let token=sessionStorage.getItem("token");
      let url="http://zhuishu.zhiask.cn/api";
      let data={
        "action": "del_task",
        "data": {
            "id": this.taskmsg.id,
            "task": this.list[index].id
        }
      }
      axios({
        method: 'post',
        url: url,
        data: JSON.stringify(data),
        headers:{
          "Content-Type": "application/json" ,//multipart/form-data;boundary=--xxxxxxx   application/json
          "Access-Token":token
        },
        contentType: "text/html;charset=utf-8",
        dataType: "json",
      })
      .then(res=>{
        console.log(res.data.data);
        if(res.data.errcode==0){
          this.$message({
            message: '删除成功',
            type: 'success'
          });
          this.list.splice(index,1)
        }else{
          this.$message.error('删除失败');
        }
      })
    },
    //新建采集
    xinjian(){
      return;
      let token=sessionStorage.getItem("token");
      let url="http://zhuishu.zhiask.cn/api";
      let data={
        "action": "del_task",
        "data": {
            "id": this.taskmsg.id,
            "task": this.list[index].id
        }
      }
      axios({
        method: 'post',
        url: url,
        data: JSON.stringify(data),
        headers:{
          "Content-Type": "application/json" ,//multipart/form-data;boundary=--xxxxxxx   application/json
          "Access-Token":token
        },
        contentType: "text/html;charset=utf-8",
        dataType: "json",
      })
      .then(res=>{
        console.log(res.data.data);
        if(res.data.errcode==0){
          this.$message({
            message: '清空成功',
            type: 'success'
          });
          this.list.splice(index,1)
        }else{
          this.$message.error('清空失败失败');
        }
      })
    },
    //清空采集任务
    remove(){
      let token=sessionStorage.getItem("token");
      let url="http://zhuishu.zhiask.cn/api";
      let data={
        "action": "cls_task",
        "data": {
            "id": this.taskmsg.id,
        }
      }
      axios({
        method: 'post',
        url: url,
        data: JSON.stringify(data),
        headers:{
          "Content-Type": "application/json" ,//multipart/form-data;boundary=--xxxxxxx   application/json
          "Access-Token":token
        },
        contentType: "text/html;charset=utf-8",
        dataType: "json",
      })
      .then(res=>{
        console.log(res.data.data);
        if(res.data.errcode==0){
          this.$message({
            message: '清空成功',
            type: 'success'
          });
          this.list.splice(index,1)
        }else{
          this.$message.error('清空失败失败');
        }
      })
    }
  },
  created(){
    this.taskmsg=JSON.parse(sessionStorage.getItem("taskmsg")) 
    this.gettasklist();
  }
}
</script>
<style>
  a,div,img,span,p,input,h1,h2,h3,h4,h5,h6,header,body,footer,ul,li{
  margin:0px;
  padding:0px;
}
.atm{
  width: 97%;
  margin-left: 32px;
  padding-top:30px;
}
.atm .top{
  display:flex;
  padding-bottom: 20px;
  border-bottom: 1px solid rgba(187, 187, 187, 1);
}
.atm .top div{
  width: 84px;
  height: 25px;
  line-height: 25px;
  border-radius: 6px;
  color: rgba(16, 16, 16, 1);
  font-size: 12px;
  text-align: center;
  font-family: Microsoft Yahei;
  border: 1px solid rgba(187, 187, 187, 1);
  margin-right: 35px;
  cursor: pointer;
}
.atm table{
  width: 100%;
  border-collapse: collapse;
  border: 1px solid rgba(187, 187, 187, 1);
}
.atm table td{
  border-collapse: collapse;
  text-align: center;
  font-size: 14px;
  height: 34px;
  padding:5px 0px;
  border-bottom: 1px solid rgba(187, 187, 187, 1);
}
.atm table .head{
  height: 34px;
  line-height: 34px;
  background-color: rgba(247, 247, 247, 1);
  text-align: center;
}
.atm table .name{
  width: 30%;
  color: rgba(16, 16, 16, 1);
  font-family: SourceHanSansSC-regular;
}
.atm table .cz{
  width: 70%;
}
.atm table .cz div{
  display: inline-block;
  width: 60px;
  height: 20px;
  border-radius: 4px;
  color: rgba(16, 16, 16, 1);
  font-size: 13px;
  text-align: center;
  font-family: Microsoft Yahei;
  border: 1px solid rgba(187, 187, 187, 1);
  cursor: pointer;
  line-height: 20px;
}
.atm table .cz .see{
  margin-right: 10px;
}
.fenye{
  width:600px;
  display: flex;
  margin:50px auto;
}
</style>


